import React, { Component } from 'react'
import { Client } from 'boardgame.io/react'

import game from './Game'
import Board from './Board'

class App extends Component {
  constructor(...props){
    super(...props)
  }
  render() {
    const GameClient = Client({game, debug: true, board: Board})
    return (
      <GameClient />
    )
  }
}

export default App;



